import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ClaimEstimateComponent } from './claim-estimate.component';
import { Route, RouterModule } from '@angular/router';
import { WidgetsModule } from 'src/app/widgets/widgets.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { MatIconModule } from '@angular/material/icon';
import { TranslateModule } from '@ngx-translate/core';
import { ErrorModule } from 'src/app/shared/error-page/error.module';
import { EstimateTableComponent } from './estimate-table/estimate-table.component';
import { EstimateDetailsComponent } from './estimate-details/estimate-details.component';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { EstimateDeclineComponent } from 'src/app/dialogs/estimate-decline/estimate-decline.component';
import { DirectiveModule } from 'src/app/helper/directive/directive.module';

const claimEstimateRoutes: Route[] = [
  {
    path: '',
    component: EstimateDetailsComponent,
    resolve: { version: VersionControlResolver },
  },
  {
    path: 'estimate-log',
    component: EstimateTableComponent,
    resolve: { version: VersionControlResolver },
  }
 
];

@NgModule({
  declarations: [
    ClaimEstimateComponent,
    EstimateTableComponent,
    EstimateDetailsComponent,
    EstimateDeclineComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(claimEstimateRoutes),
    WidgetsModule,
    SharedModule,
    MatIconModule,
    ErrorModule,
    TranslateModule,
    DirectiveModule
  ],

  exports: [
    RouterModule
  ]
})
export class EstimateModule { }
